import "./NewUser.css";

export const NewUser = () => {
  return (
    <div className="NewUser">
      <h1 className="NewUserTitle">Creat New User</h1>
      <form className="NewUserForm">
        <div className="NewUserItem">
          <label>Username</label>
          <input type="text" placeholder="Create User Name" />
        </div>
        <div className="NewUserItem">
          <label>Full Name</label>
          <input type="text" placeholder="Enter Your Full Name" />
        </div>
        <div className="NewUserItem">
          <label>eM@il</label>
          <input type="email" placeholder="Enter Your email" />
        </div>
        <div className="NewUserItem">
          <label>Password</label>
          <input type="password" placeholder="Create New Passowrd" />
        </div>
        <div className="NewUserItem">
          <label>Phone Number</label>
          <input type="password" placeholder="Enter your Phone Number" />
        </div>
        <div className="NewUserItem">
          <label>Address</label>
          <input type="password" placeholder="Enter your Address" />
        </div>
        <div className="NewUserItem">
          <label>Gendeer</label>
          <div className="NewUserGender">
            <input type="radio" name="gender" id="male" value="male" />
            <label for="male">Male</label>
            <input type="radio" name="gender" id="female" value="female" />
            <label for="female">Female</label>
            <input type="radio" name="gender" id="others" value="others" />
            <label for="others">Others</label>
          </div>
        </div>
        <div className="NewUserItem">
            <label>Active</label>
            <select className="NewUserSelect" name="active" id="active">
                <option value="yes">Yes</option>
                <option value="no">No</option>
            </select>
        </div>
        <button className="NewUserBtn">Create Account</button>
      </form>
    </div>
  );
};
