import { Chart } from "../../Components/Chart/Chart";
import { Featured } from "../../Components/FeaturedInfo/Featured";
import { WidGetLg } from "../../Components/WidGetLarge/WidGetLg";
import { Widget } from "../../Components/WidGetSmall/Widget";
import { userData } from "../../DummyData";
import "./Home.css";

export const Home = () => {
  return (
    <div className="Home">
      <Featured />
      <Chart
        data={userData}
        title="User Analytics"
        grid
        dataKey="Active User"
      />
      <div className="HomeWidget">
        <Widget/>
        <WidGetLg/>
      </div>
    </div>
  );
};
