import "./Chart.css";
import {
  LineChart,
  Line,
  XAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import "../../DummyData";

export const Chart = ({ title, data, dataKey, grid }) => {
  return (
    <div className="ChartContainer">
      <h3 className="ChartTitle">{title}</h3>
      <ResponsiveContainer width="100%" aspect={4 / 1}>
        <LineChart data={data}>
          <XAxis dataKey="name" stroke="#555000" />
          <Line type="monotone" dataKey={dataKey} stroke="#68ccf4" />
          <Tooltip />
          {grid && <CartesianGrid stroke="#eeeded" strokeDasharray="5 5" />}
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};
