import React from "react";
import "./TopNav.css";
import { NotificationsNone, Language, Settings } from "@material-ui/icons";

export const TopNav = () => {
  return (
    <div className="topbar">
      <div className="topbarWrapper">
        <div className="topLeft">
          <span className="logo">Rajiv.</span>
        </div>
        <div className="topRight">
          <div className="topbarIconContainer">
            <NotificationsNone />
            <span className="topIconBadge">2</span>
          </div>
          <div className="topbarIconContainer">
            <Language />
            <span className="topIconBadge">2</span>
          </div>
          <div className="topbarIconContainer">
            <Settings />
          </div>
          <img src="https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/cf1975137504807.620c824516dc8.png" alt="" className="topAvatar" />
        </div>
      </div>
    </div>
  );
};
